import React from "react";
import { Container, Row, Col } from "react-bootstrap";

const Footer = () => {
  return (
    <>
      <Container
        fluid
        className="animate__animated animate__backInUp animate__duration-2s"
      >
        <Row className="">
          <Col>
            <h7 style={{ color: "" }}>
              <span title="Github">
                <a
                  href="https://github.com/TejasJavalkar"
                  target="_blank"
                  rel="noreferrer"
                >
                  <i
                    className="icon bx bxl-git"
                    style={{ color: "#00e3ea", fontSize: "34px" }}
                  ></i>
                </a>
              </span>
              <span className="pl-2" title="LinkedIn">
                <a
                  href="https://www.linkedin.com/in/tejas-javalkar-b4b1a0180/?originalSubdomain=in"
                  target="_blank"
                  rel="noreferrer"
                >
                  <i
                    className="icon bx bxl-linkedin-square"
                    style={{ color: "#00e3ea", fontSize: "34px" }}
                  ></i>
                </a>
              </span>
              <span title="Facebook">
                <a
                  href="https://m.facebook.com/"
                  target="_blank"
                  rel="noreferrer"
                >
                  <i
                    className="icon bx bxl-facebook-square"
                    style={{ color: "#00e3ea", fontSize: "34px" }}
                  ></i>
                </a>
              </span>
            </h7>
          </Col>
          <Col className="text-end p-1" md={6}>
            <p
              className="text-light "
              style={{
                fontFamily: "Noto Serif, serif",
                display: "inline-block",
              }}
            >
              <span
                className="link"
                style={{
                  color: "#17d482",

                  textDecoration: "underline",
                }}
              >
                <i
                  className="far fa-copyright"
                  style={{ color: "#ffffff" }}
                ></i>
                &nbsp; TejasJavalkar
              </span>
            </p>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default Footer;
